import React from "react";
import Image from "next/image";
import Logo from "../../public/images/logo.png";
const Nav = () => {
  return (
    <header>
      <nav className="flex items-center justify-between mx-auto py-4 px-4">
        <div className="flex items-center font-bold text-2xl gap-2">
          <Image width={40} height={40} alt="Logo" src={Logo}></Image>
          <span className="text-primary">VPN</span>
        </div>
        <ul className=" space-x-4  hidden sm:flex ">
          <li className="text-gray-500 hover:text-black">About</li>
          <li className="text-gray-500 hover:text-primary">Features</li>
          <li className="text-gray-500 hover:text-primary">Pricing</li>
          <li className="text-gray-500 hover:text-primary">Testimonials</li>
          <li className="text-gray-500 hover:text-primary">Help</li>
        </ul>
        <div className="space-x-4">
          <button className="text-gray-600 hover:text-white rounded-full p-3  cursor-pointer ">
            Sign In
          </button>
          <button className="text-primary rounded-full p-3 bg-white border-2 border-primary cursor-pointer ">
            Sign Out
          </button>
        </div>
      </nav>
    </header>
  );
};

export default Nav;
